body {
  // font-family: 'Montserrat', sans-serif;
  @extend %ubuntu-light;
  color: $white;
  font-size: 1.7rem;
  line-height: 2.5rem;
  // системные шрифты
  // font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  // background-color: $gray;
}

.none {
  display: none;
}

.container {
  max-width: 127rem;
  padding: 0 1.5rem;
  margin: 0 auto;
}

.page-section {
  padding: 7.4rem 0 5rem;
  min-height: 100vh;

  background: url("../img/bg-2.jpg") no-repeat;
  background-position: top center;
  background-size: cover;

  position: relative;
}

.title {
  @extend %sf-regular;
  @include font($f96, normal, 600, $white);
}

.scroll__btn {
  padding: 1.3rem 0 3rem;

  text-align: center;

  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}

.arrow {
  &-down {
    fill: transparent;
    width: 1.5rem;
    height: 0.7rem;
  }

  &-up {
    transform: rotate(180deg);
  }
}